<template>
	<transition name="slider">
		<music-list :songs="curruntDiscList" :bgurl="bgurl" :title="title"></music-list>
	</transition>
</template>
</template>

<script type="text/javascript">
	import { getDiscList } from 'api/recommend'
	import {ERR_OK} from 'api/config'
  import { playlistMixin } from 'common/js/mixin'
  import { creatSong, processSongsUrl} from 'common/js/song'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import musicList from '../music-list/musicList'
	export default {
    mixins: [playlistMixin],
    computed: {
      ...mapGetters(['currentDisc']),
      bgurl() {
        return this.currentDisc.imgurl
      },
      title() {
        return this.currentDisc.dissname
      }
    },
    data () {
      return {
        curruntDiscList: [],
      }
    },
    methods: {
      _normalLizeSong(musicData) {
        let songItem = creatSong(musicData)
        this.curruntDiscList.push(songItem)
      }
    },
    components: {
      musicList
    },
    created() {
    	getDiscList(this.currentDisc.dissid).then((res)=>{ 
      	if (res.data.code === ERR_OK) {
          let cdList = res.data.cdlist[0].songlist
          cdList.forEach((item) => {
            this._normalLizeSong(item)
          })
          processSongsUrl(this.curruntDiscList)
        }
      })
    }
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  .disc{
  	position: fixed
  	top: 0
  	bottom: 0
  	width: 100%
  	background: pink
  }
  .slider-enter-active,.slider-leave-active{
    transition: all .3s
  }
  .slider-enter,.slider-leave-to{
    transform: translate3d(100%, 0, 0)
  }
</style>


